<template>
  <dir>
    <Editor :init="init" v-model="content"></Editor>
  </dir>
</template>

<script>
import Editor from '@tinymce/tinymce-vue'
import './tinymce.min.js'
import './icons/default/icons.min.js'
import './themes/silver/theme.min.js'
import './langs/zh_CN.js'

import './plugins/preview/plugin.min.js'
import './plugins/code/plugin.min.js'
import './plugins/wordcount/plugin.min.js'
import './plugins/fullscreen/plugin.min.js'
import './plugins/table/plugin.min.js'
import './plugins/image/plugin.min.js'
import './plugins/imagetools/plugin.min.js'
import './plugins/media/plugin.min.js'
import './plugins/codesample/plugin.min.js'
import './plugins/lists/plugin.min.js'

export default {
  components: { Editor },
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      init: {
        language: 'zh_CN',
        height: '500px',
        plugins: 'code preview wordcount fullscreen table imagetools image media codesample lists',
        branding: false,
        toolbar: [
          'undo redo | styleselect |fontsizeselect| bold italic underline strikethrough |alignleft aligncenter alignright alignjustify |blockquote removeformat |numlist bullist table', 'preview paste code codesample |image media'
        ],
        images_upload_handler: async (blobInfo, succFun, failFun) => {
          const formdata = new FormData()
          formdata.append('file', blobInfo.blob(), blobInfo.name())
          const { data: res } = await this.$http.post('upload', formdata)
          succFun(res.url)
          failFun(this.$message.error('上传图片失败'))
        },
        imagetools_cors_hosts: ['*'],
        imagetools_proxy: ''
      },
      content: this.value
    }
  },
  watch: {
    value (newV) {
      this.content = newV
    },
    content (newV) {
      this.$emit('input', newV)
    }
  }
}
</script>

<style>
@import url('./skins/ui/oxide/skin.min.css');
</style>
